<template>
  <div class="signin">
    <div>
      <SelectCountry></SelectCountry>
      <div class="logo"><img src="../assets/img/signin/logo.png" alt="" /></div>
      <div>
        <div style="display: flex; justify-content: center">
          <div class="bodybg">
            <div class="body">
              <div class="body_title">
                {{ HTML_SIGUP[$store.state.language].TITLE }}
              </div>
              <div>
                <input type="text" class="body_input" :value="leader" @blur="inputvalueChange" />
              </div>

              <div class="prompt">
                {{ HTML_SIGUP[$store.state.language].PROMPT }}
              </div>
              <div class="newTeam">
                <!-- 可以不用 name属性 -->
                <label for="noNewTeam">
                  <input
                    type="radio"
                    id="noNewTeam"
                    name="isNewTeam"
                    value="false"
                    v-model="isNewTeam"
                  />
                  {{$store.state.language==`CN`?'与推荐人同组':'Same group as recommender'}}
                </label>
                <label for="yesNewTeam">
                  <input
                    type="radio"
                    id="yesNewTeam"
                    name="isNewTeam"
                    value="true"
                    v-model="isNewTeam"
                  />
                  {{$store.state.language==`CN`?'创建独立新组':'Create independent new group'}}
                  
                </label>
              </div>

              <div class="divbuttonlin">
                <div class="divbutton"  @click="clickSignin">
                  <div class="button_title">
                    {{ HTML_SIGUP[$store.state.language].BUTTON }}
                  </div>
                </div>
              </div>
              <div class="selectMaix">
                <div class="maixTitle">
                  <span class="maixTitleNewSage">NewSage</span>
                  <span class="maixTitleNewSage_gt">
                    &gt;&gt;&gt;&gt;&gt;&gt;&gt;</span
                  ><span class="maixTitleNewSage_x">X3</span>
                </div>

                <div class="selectMaix_x3">
                  <div class="x3_list">
                    <div
                      class="ternary"
                      v-for="(item, index) in $store.state.newsage.levelPrice"
                      :key="item"
                    >
                      <label
                        :class="
                          x3Maix.indexOf(index) > -1
                            ? 'matrix-root__active'
                            : 'matrix-root_bg'
                        "
                      >
                        <span class="matrix-level matrix-level__active">{{
                          index.split("level")[1]
                        }}</span
                        ><span class="matrix-price">{{ item }}</span>
                        <input
                          type="checkbox"
                          :value="index"
                          :id="'x3' + item"
                          v-model="x3Maix"
                          @click="clickx3Item(1, index)"
                        />
                      </label>
                    </div>
                  </div>

                </div>

                <div class="maixTitle">
                  <span class="maixTitleNewSage">NewSage</span>
                  <span class="maixTitleNewSage_gt">
                    &gt;&gt;&gt;&gt;&gt;&gt;&gt;</span
                  ><span class="maixTitleNewSage_x">X6</span>
                </div>

                <div class="selectMaix_x3">
                  <div class="x3_list">
                    <div
                      class="ternary"
                      v-for="(item, index) in $store.state.newsage.levelPrice"
                      :key="item"
                    >
                      <label
                        :class="
                          x6Maix.indexOf(index) > -1
                            ? 'matrix-root__active'
                            : 'matrix-root_bg'
                        "
                      >
                        <span class="matrix-level matrix-level__active">{{
                          index.split("level")[1]
                        }}</span
                        ><span class="matrix-price">{{ item }}</span>
                        <input
                          type="checkbox"
                          :value="index"
                          :id="'x6' + item"
                          v-model="x6Maix"
                          @click="clickx6Item(1, index)"
                        />
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div style="display: flex; justify-content: center">
          <div class="divhelp">
            <samp>&lt;&lt;&lt;&lt;&lt;&lt;</samp>
            <samp>{{$store.state.language==`CN`?'注册可购买多个矩阵':'Register to purchase multiple matrices'}}</samp>
            <!-- <samp>{{ HTML_SIGUP[$store.state.language].DESCRIPTION }}</samp> -->
            <samp>&gt;&gt;&gt;&gt;&gt;&gt;</samp>
          </div>
        </div>
      </div>

      <div></div>
    </div>
  </div>
</template>

<script>
export default {
  components:{
    SelectCountry
  },
  computed: {
  },
  data() {
    return {
      x3Maix: ["level1"],
      x6Maix: ["level1"],
      isNewTeam: false,
      HTML_SIGUP,
      leader: this.$route.query.leader,
      factory: this.$store.state.factory,
      newsage: this.$store.state.newsage,
    };
  },
  methods: {
   ...mapActions([
      ACTION_TYPES.INIT_NEWSAGE,
      ACTION_TYPES.INIT_FACTORY,
      ACTION_TYPES.INIT_NEWSAGE_ROUTER,
      "displayLoding",
      "randomDisplayData",
      "displayTips"
    ]),
    //游戏启动次数
    [FACTORY_TYPES.FACTORY_LAST_ID]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_LAST_ID,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //代币地址
    [FACTORY_TYPES.FACTORY_DETO_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_DETO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //路由工厂地址
    [FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //newsage工厂地址
    [FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //WETH地址
    [FACTORY_TYPES.FACTORY_WETH_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_WETH_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap 资金池地址
    [FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //uniswap 路由地址
    [FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //历史游戏地址查询 获得路由地址 和 游戏地址
    [FACTORY_TYPES.EVENT_FACTORY_GAMES](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: { id },
        methodName: FACTORY_TYPES.EVENT_FACTORY_GAMES,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    //当前游戏配置信息 获得路由地址 和 游戏地址
    [FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS]() {
      const that = this;
      let temObj = {
        contractIndexToUse: "Factory", //合约名称
        value: 0,
        requestParams: {},
        methodName: FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    getFactory() {
      const that = this;
      let factory = that.factory;
      const promises = [
        //游戏启动次数
        that[FACTORY_TYPES.FACTORY_LAST_ID](),
        //代币地址
        that[FACTORY_TYPES.FACTORY_DETO_ADDRESS](),
        //路由工厂地址
        that[FACTORY_TYPES.FACTORY_NEWSAGE_ROUTER_FACTORY_ADDRESS](),
        //newsage工厂地址
        that[FACTORY_TYPES.FACTORY_NEWSAGE_FACTORY_ADDRESS](),
        //WETH地址
        that[FACTORY_TYPES.FACTORY_WETH_ADDRESS](),
        //uniswap 资金池地址
        that[FACTORY_TYPES.FACTORY_UNISWAPV2_PAIR_ADDRESS](),
        //uniswap 路由地址
        that[FACTORY_TYPES.FACTORY_UNISWAPV2_ROUTER02_ADDRESS](),
        //当前游戏配置信息 获得路由地址 和 游戏地址
        that[FACTORY_TYPES.EVENT_LAST_ROUTER_NEWSAGE_ADDRESS](),
      ];
      return new Promise(function (resolve, reject) {
        Promise.all(promises).then((arr) => {
          factory.lastId = Number(arr[0]);
          factory.DETOAddress = arr[1];
          factory.NewSageRouterFactory = arr[2];
          factory.SmartMatrixNewsageFactory = arr[3];
          factory.WETHAddress = arr[4];
          factory.uniswapV2PairAddress = arr[5];
          factory.uniswapV2Router02Address = arr[6];
          factory.SmartMatrixNewsageAddress =
            arr[7]["SmartMatrixNewsageAddress"];
          factory.NewSageRouterAddress = arr[7]["NewSageRouterAddress"];
          that[ACTION_TYPES.INIT_FACTORY]({ factory });
          arr ? resolve(arr) : reject();
        });
      });
    },
    CallSmartMatrixNewsage(obj) {
      const that = this;
      return new Promise(function (resolve, reject) {
        that.$root.callToAccessBlockchain({
          value: obj.value, //转账值
          requestParams: obj.requestParams, //参数
          contractIndexToUse: obj.contractIndexToUse, //合约名称
          methodName: obj.methodName, //方法名称
          managerIndex: "NewsageManager",
          pairtType: obj.pairtType,
          callback: (isSuccess) => {
            isSuccess != undefined ? resolve(isSuccess) : reject();
          },
        });
      });
    },
    //根据id查询地址
    [NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](id) {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: 0,
        requestParams: { id },
        methodName: NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    
    clickx3Item(index, data) {
      let tempselectitem = Number(data.split("level")[1]);
      if (tempselectitem == 1) {
        this.x3Maix = ["level1"];
      } else {
        this.x3Maix = ["level1"];
        for (let i = 2; i <= tempselectitem; i++) {
          this.x3Maix.push(`level${i}`);
        }
      }
    },
    clickx6Item(index, data) {
      let tempselectitem = Number(data.split("level")[1]);
      if (tempselectitem == 1) {
        this.x6Maix = ["level1"];
      } else {
        this.x6Maix = ["level1"];
        for (let i = 2; i <= tempselectitem; i++) {
          this.x6Maix.push(`level${i}`);
        }
      }
    },
    inputvalueChange(event){
      let that = this;
      this.leader=Number(event.target.value);
      that.getFactory().then(()=>{
        that[NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](this.leader).then((res) => {
          
          if(res=='0x0000000000000000000000000000000000000000'){
              that.displayTips({isTips:true,tipsMs:'推荐人编号不存在'});
              setTimeout(() => {
                that.displayTips({isTips:false,tipsMs:''});
              }, 2500);
          }
        })
      })
    },
    filterX3Arr(arr){
      let that = this;
      let newarr=Array.from(new Set(arr))
      let filterarr = newarr.filter(function (x) {
          return !that.newsage.usersActiveX3Levels[`level${x}`]
      });
      let sortarr=filterarr.sort((a, b)=>{
        return a - b;
      })
      return sortarr
    },
    filterX6Arr(arr){
      let that = this;
      let newarr=Array.from(new Set(arr))
      let filterarr = newarr.filter(function (x) {
          return !that.newsage.usersActiveX6Levels[`level${x}`]
      });
      let sortarr=filterarr.sort((a, b)=>{
        return a - b;
      })
      return sortarr
    },
    taximeter(X3levels,X6levels){
      let that = this;
       let x3Price =0;
      if(X3levels.length>0){
        x3Price = X3levels.reduce(function (x, y) {
          if(y>=1&&y<=12){
            return x + that.newsage.levelPrice[`level${y}`];
          }else{
            return x;
          }
        },0);
      }
     let x6Price=0;
     if(X6levels.length>0){
        x6Price = X6levels.reduce(function (x, y) {
          if(y>=1&&y<=12){
            return x + that.newsage.levelPrice[`level${y}`];
          }else{
            return x;
          }
        },0);
      }
      return x3Price+x6Price;
    },
    clickSignin(){
      let that = this;
      
      that.getFactory().then(()=>{
        that[NEWSAGE_TYPES.UPDATE_NEWSAGE_ID_TO_ADDRESS](this.leader).then((res) => {
          
          if(res!='0x0000000000000000000000000000000000000000'){
            let newx3arr = [];
            let newx6arr=[];
            for(let i=0;i<this.x3Maix.length;i++){
              newx3arr.push(Number(this.x3Maix[i].split("level")[1]));
            }
            for(let i=0;i<this.x6Maix.length;i++){
              newx6arr.push(Number(this.x6Maix[i].split("level")[1]));
            }
           
            
            that.btnregistrationExt(that.isNewTeam,res,newx3arr,newx6arr,(res)=>{
              if(res&&res.tx){
                
                that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'注册成功':'registration success'});
                setTimeout(() => {
                  that.displayTips({isTips:false,tipsMs:''});
                  that.$router.push(`/Console?selectAddress=${that.$store.state.user.coinbase}`);
                  
                }, 2500);
              }else{
                
                that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'注册失败':'registration failed'});
                setTimeout(() => {
                  that.displayTips({isTips:false,tipsMs:''});
                }, 2500);
              }
              
            });
          }else{

              that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'推荐人编号不存在':'Referrer ID does not exist'});
              setTimeout(() => {
                that.displayTips({isTips:false,tipsMs:''});
              }, 2500);
          }
        })
      })
     
     
      
     
      
      //检查推荐人是否有效
      //检查用户是否购买过
      //如果购买过 
      //如果没有购买提交请求

    },
    //购买
    [NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL](totalPrice,X3levels,X6levels){
      const that = this;
      
      let temObj={
        value:that.$store.state.web3.instance().utils.toWei(`${totalPrice}`,'ether'),
        requestParams:{X3levels,X6levels},
        methodName:NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL
      };
     return that.CallSmartMatrixNewsage(temObj);
    },
   
    //注册
    [NEWSAGE_TYPES.EVENT_NEWSAGE_REGIS_TRATION_EXT](isNewTeam,refferrerAddress,totalPrice,X3levels,X6levels){
      const that = this;//
     
      let temObj={
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value:that.$store.state.web3.instance().utils.toWei(`${totalPrice}`,'ether'),
        requestParams:{isNewTeam,refferrerAddress,X3levels,X6levels},
        methodName:NEWSAGE_TYPES.EVENT_NEWSAGE_REGIS_TRATION_EXT
      };
     return that.CallSmartMatrixNewsage(temObj);
    },
    btnregistrationExt(isNewTeam,refferrerAddress,X3levels,X6levels,cb){
      let that = this;
      X3levels=that.filterX3Arr(X3levels);//数组过滤
      X6levels=that.filterX6Arr(X6levels);//数组过滤
       console.log(X3levels)
      console.log(X6levels)
       let totalPrice=that.taximeter(X3levels,X6levels);
       console.log(totalPrice)
      if(totalPrice>0){
        that[NEWSAGE_TYPES.EVENT_NEWSAGE_REGIS_TRATION_EXT](isNewTeam,refferrerAddress,totalPrice,X3levels,X6levels).then(res=>{
          
          if(cb){cb(res);}
        });
      }
    }
  },
};
import { mapActions } from "vuex";
import {
  ACTION_TYPES,
  HTML_SIGUP,
  NEWSAGE_TYPES,
  FACTORY_TYPES
} from "@/util/constants";
import SelectCountry from "@/components/SelectCountry.vue";
</script>

